<template>
  <div class="user">
    <div
      class="top"
      :style="{ backgroundImage: `url('${profile.backgroundUrl}')` }"
    >
      <img
        src="../assets/images/icon/returnKey_1.png"
        @click="$router.go(-1)"
        class="exit"
      />
      <img src="../assets/images/shade.png" class="shade" />
    </div>
    <div class="avatar">
      <div class="icon">
        <img :src="profile.avatarUrl + '?param=120y120'" />
      </div>
      <div class="content">
        <p class="userName">
          {{ profile.nickname }}
          <span
            ><img
              v-if="data.identify != undefined"
              :src="data.identify.imageUrl"
              alt=""
          /></span>
        </p>
        <!-- <p class="signature"><span>签名: </span>{{ profile.signature }}</p> -->
        <p class="Fans">
          <span>{{ profile.follows }} <i>关注</i> </span>
          <span>{{ profile.followeds }} <i>粉丝</i> </span>
          <span>Lv.{{ data.level }}</span>
        </p>
      <Loading v-if="isLoading"></Loading>
      </div>
    </div>
    <div class="bottom">
      <h3>基本信息</h3>
      <p v-if="profile.artistName!=undefined">
        艺名: <span>{{ profile.artistName }}</span>
      </p>
      <p>
        性别: <span>{{ profile.gender==1?'男':'女' }}</span>
      </p>
      <!-- <p>
        地址: <span>{{ profile.province }}</span>
      </p> -->
      <p>
        村龄: <span>{{ parseInt(data.createDays/365) }} 年</span>
      </p>
      <p>
        个人简介: <span>{{ profile.signature }}</span>
      </p>
    </div>
  </div>
</template>
<script>
import Loading from "@/components/Loading";

export default {
  name: "User",
  props: ["userId"],
  components: { Loading },
  data() {
    return {
      data: {},
      isLoading: true,
      profile: {},
    };
  },
  created() {
    // console.log(this.$route.params.id);
    this.$axios
      .get("/user/detail?uid=" + this.$route.params.id)
      .then((data) => {
        return data.data;
      })
      .then((item) => {
        this.data = item;
        this.profile = item.profile;
        this.isLoading=false;

      });
  },
};
</script>
<style lang="less" scoped>
.user {
    overflow: hidden;
  .top {
    width: 100%;
    height: 280px;
    background-color: rgb(141, 141, 131);
    background-size: 100% auto;
    position: relative;
    .exit {
      margin: 3%;
      float: left;
      position: fixed;
      top: 0;
      left: 0;
      background-color: rgba(104, 104, 104, 0.4);
    }
    .shade {
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
  .avatar {
    width: 88%;
    height: 140px;
    background-color: rgba(255, 255, 255, 0.9);
    box-shadow: 0px 0px 15px -1px #ddd;
    position: relative;
    top: -40px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 20px;
    .icon {
      width: 96px;
      height: 96px;
      border-radius: 50%;
      overflow: hidden;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, -50%);
      border: 2px solid rgb(255, 255, 255);
      img {
        width: 100%;
      }
    }
  }
  .content {
    position: absolute;
    top: 60px;
    left: 50%;
    transform: translateX(-50%);
    .userName {
      font-size: 1.2rem;
      font-weight: 600;
      span {
        width: 20px;
        height: 20px;
        display: inline-block;
        img {
          width: 100%;
          height: 100%;
          vertical-align: top;
          border: 1px solid white;
        }
      }
    }
    .signature {
      font-size: 0.9rem;
      color: rgb(121, 121, 121);
      span {
        font-weight: 600;
        color: black;
      }
    }
    .Fans {
      width: 80vw;
      font-size: 0.8rem;
      margin: 10px 0;
      span {
        &:nth-child(2) {
          margin: 0 20px;
        }
      }
      i {
        font-style: normal;
        color: rgb(156, 156, 156);
      }
    }
  }
  .bottom {
    width: 88%;
    // height: 200px;
    background-color: rgba(255, 255, 255, 0.9);
    margin: 0 auto;
    box-shadow: 0px 0px 15px -1px #ddd;
    border-radius: 20px;
    padding: 3%;
    box-sizing: border-box;
    text-align: left;
    margin-bottom: 70px;
    h3 {
      text-align: left;
      margin-bottom: 10px;
      margin-top: 10px;
    }
    p {
      text-align: left;
      color: rgb(143, 143, 143);
      margin: 6px 0;
      span{
          color: rgb(66, 65, 65);
      }
    }
  }
}
</style>
